<template>
    <div class="img-back" style="position: relative; height: 100%!important;">
        <v-container class="login" style="position: relative; padding-bottom: 40px; height: 100%; max-width: 550px">

            <v-col cols="12">
                <v-card-title class="text-center justify-center pa-3 white--text">
                    {{accessToYourAccountText}}
                </v-card-title>

                <v-card style="padding-bottom: 8px" class="text--center justify-center">
                    <div align="center" style="padding-top: 30px">
                        <img style="width: 60px" src="/LogoWeBabble.png"/>
                    </div>

                    <v-container class="pa-3" style="padding-top: 10px!important;">
                        <v-form v-model="validForm" class="pa-1">
                            <v-text-field
                                    v-model="email"
                                    :rules="[rules.required]"
                                    :label="emailText"
                                    required
                            ></v-text-field>

                            <v-text-field
                                    v-model="password"
                                    :rules="[rules.required]"
                                    :append-icon="showPassword ? 'mdi-eye' : 'mdi-eye-off'"
                                    :type="showPassword ? 'text' : 'password'"
                                    :label="passwordText"
                                    @click:append="showPassword = !showPassword"
                            ></v-text-field>
                        </v-form>

                        <v-btn block color="primary" dark @click="loginUser"
                               :disabled="buttonIsDisabled">{{toLogInText}}
                        </v-btn>
                    </v-container>
                </v-card>
            </v-col>
            <v-col cols="12" align="center" style="padding-top: 2px">
                <router-link class="white--text" style="font-size: 14px" to="/send-change-password-email">
                    {{haveYouForgottenYourPasswordText}}
                </router-link>
            </v-col>

            <v-col class="pa-0" cols="12" align="center" id="sign-up-now-claim"
                   style="position: absolute; bottom: 0px; margin-left: -12px">
                <p class="white--text" style="font-size: 14px">{{stillDontHaveAnAccountText}}
                    <router-link to="/sign-up" class="white--text" style="font-size: 14px">
                        {{registerNowText}}
                    </router-link>
                </p>
            </v-col>


            <v-snackbar
                    v-model="snackbar"
                    :timeout="4000"
            >
                {{ snackbarText }}
                <v-btn
                        color="primary"
                        text
                        @click="snackbar = false"
                >
                    {{closeText}}
                </v-btn>
            </v-snackbar>
        </v-container>
    </div>
</template>

<script>
    import {mapActions} from 'vuex';

    // P치gina de inicio de sesi칩n
    // @group Cuenta
    export default {
        name: "Login",
        data() {
            return {
                validForm: false,
                email: '',
                password: '',
                showPassword: false,
                buttonIsDisabled: true,
                snackbar: false,
                snackbarText: '',
                accessToYourAccountText: null,
                registerNowText: null,
                haveYouForgottenYourPasswordText: null,
                stillDontHaveAnAccountText: null,
                closeText: null,
                toLogInText: null,
                passwordText: null,
                emailText: null,
                rules: {
                    required: value => !!value || this.$i18n.t('messages.RULE_TEXT_MANDATORY_FIELD')
                }
            }
        },
        methods: {
            ...mapActions('user', ['login']),

            // @vuese
            // Trata de iniciar sesi칩n con los datos introducidos por el usuario y, si lo consigue, redirige a este al Dashboard de la aplicaci칩n
            loginUser: function () {
                let self = this;
                self.login({
                    email: self.email,
                    password: self.password
                }).then(() => {
                    self.$router.replace('dashboard');
                })
                    .catch(function (error) {
                        self.snackbarText = error;
                        self.snackbar = true;
                    });

            }
        },
        created() {
            this.accessToYourAccountText = this.$i18n.t('messages.TEXT_ACCESS_TO_YOUR_ACCOUNT');
            this.registerNowText = this.$i18n.t('messages.TEXT_REGISTER_NOW');
            this.haveYouForgottenYourPasswordText = this.$i18n.t('messages.TEXT_HAVE_YOU_FORGOTTEN_YOUR_PASSWORD');
            this.stillDontHaveAnAccountText = this.$i18n.t('messages.TEXT_STILL_DONT_HAVE_AN_ACCOUNT');
            this.closeText = this.$i18n.t('messages.TEXT_CLOSE');
            this.toLogInText = this.$i18n.t('messages.TEXT_TO_LOG_IN');
            this.passwordText = this.$i18n.t('messages.TEXT_PASSWORD');
            this.emailText = this.$i18n.t('messages.TEXT_EMAIL');
        },
        watch: {
            validForm: function (value) {
                if (value) {
                    this.buttonIsDisabled = false
                } else {
                    this.buttonIsDisabled = true
                }
            }
        }
    }
</script>

<style scoped>

